import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
// import roleAdmission from '../QrCode/QrCode';
import {getBed,getRoom,getRole,getWard,getHospital,getDepartment} from './../Apis.js'
//import rolesData from './rolesData'

class Role extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      showroleInfo:false
    };
  }

  componentDidMount() {
    this.getRole(this.props.match.params.id)
  
  }
  getRole=async(id)=>{
    const responseData= await getRole(id);
    console.log(responseData)
      if(responseData.status == 200){
        this.setState({
           
          items: responseData.data
        });
        this.getHospital( responseData.data.hospital_id)
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getHospital=async(id)=>{
    const responseData= await getHospital(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          hospitalName: responseData.data.name
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  showroleInfo=()=>{
    this.setState({showroleInfo:true})
  }
  render() {

    const role = this.state.items; //rolesData.find( role => role.id.toString() === this.props.match.params.id)

    let roleName = '-';

    if(role.roles !== undefined && role.roles.length !== 0){
      roleName = role.roles[0].name;
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={3}>
          </Col>
          <Col lg={6}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>role Id: {this.props.match.params.id}</strong>
                {/* <button onClick={this.showroleInfo} className='btn btn-success  rolebtn'>Enter Admission Info</button> */}
              </CardHeader>
              <CardBody>
              
                  <Table responsive striped hover>
                  <tbody>                    
                    <tr>
                      <td>Name:</td>
                      <td><strong>{role.name}</strong></td>
                    </tr>
                    <tr>
                      <td>Hospital Name:</td>
                      <td><strong>{this.state.hospitalName}</strong></td>
                    </tr>
                    
                    <tr>
                      <td>Status:</td>
                      <td><strong>{role.status==1?'Enable':'Disable'}</strong></td>
                    </tr>
                
                
                  </tbody>
                </Table>
              
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Role;
